 .wrapper {
     margin: 0 auto;
     width: 80vw;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
 }

 .wrapper input {
     color: black;
 }

 .header {
     font-size: 48px;
     font-weight: bold;
     width: inherit;
     text-align: center;
     margin: 20px 0;
 }

 .content {
     color: black;
     padding: 18px 30px;
     margin: 15px;
     width: 60%;
     outline: none;
     border-radius: 10px;
     border: 1px solid #292626;
     font-size: 20px;
     min-height: 200px;
     background-color: white;
 }


 .url {
     width: 60%;
 }

 .submit {
     background-color: #3861fb;
     color: #fff;
     border: none;
     outline: none;
     width: 30%;
     border-radius: 10px;
     padding: 15px 30px;
     font-weight: bold;
     cursor: pointer;
     margin: 40px;
 }

 .submit:hover,
 .submit:disabled {
     background-color: #829dff;
 }


 @media only screen and (max-width:500px) {
     .wrapper {
         margin: 0 auto;
         width: 100vw;
         display: flex;
         flex-direction: column;
         align-items: center;
         justify-content: center;
     }

 }