.blogsWrapper {
    margin: 50px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.blog {
    background-color: rgb(12, 78, 72);
    color: white;
    border-radius: 10px;
    width: 40%;
    padding: 16px;
    margin: 40px 20px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.blog img {
    width: 300px;
    height: 300px;
    border-radius: 10px;
}

.blog h1 {
    text-align: left;
    margin: 15px 0;
    background-color: transparent;
    color: #fff;
}

.blog:hover h1 {
    color: #3861fb;
}

.blog p {
    margin-top: 20px;
    font-weight: bold;
}


@media only screen and (max-width:430px) {

    .blog {
        width: 80%;
        padding: 16px;
        margin: 40px 20px;
        cursor: pointer;
    }

    .blog img {
        width: 250px;
        height: 250px;
        border-radius: 10px;
    }

}

@media only screen and (min-width:430px) and (max-width:550px) {
    .blog {
        width: 80%;
        padding: 16px;
        margin: 40px 20px;
        cursor: pointer;
    }

    .blog img {
        width: 300px;
        height: 300px;
        border-radius: 10px;
    }

}


@media only screen and (min-width:550px) and (max-width:675px) {
    .blog img {
        width: 200px;
        height: 200px;
        border-radius: 10px;
    }

}


@media only screen and (min-width:675px) and (max-width:910px) {
    .blog img {
        width: 250px;
        height: 250px;
        border-radius: 10px;
    }

}
@media only screen and (min-width:910px) and (max-width:1500px) {
    .blog img {
        width: 350px;
        height: 350px;
        border-radius: 10px;
    }

}